









import Vue from 'vue';

import Img from './Img.vue';

export default Vue.extend({
  name: 'ProgressBar',
  props: {
    size: Number,
    progress: Number,
    label: String,
  },
  data() {
    return {
      progressWidth: 20,
    };
  },
  computed: {
    barSize(): Number {
      return Number(this.size) || 250;
    },
  },
  mounted() {
    setTimeout(() => {
      this.progressWidth = (Number(this.progress) * Number(this.barSize)) / 100;
    }, 1);
  },
});
