










import Vue from 'vue';

export default Vue.extend({
  name: 'Img',
  props: {
    classProp: String,
    width: String,
    height: String,
    src: String,
  },
  data() {
    return {
      loaded: false,
    };
  },
  methods: {
    onLoad() {
      this.loaded = true;
    },
  },
});
